'use client';

import { useDropzone } from 'react-dropzone';
import { motion } from 'framer-motion';
import { useEffect, useState } from 'react';

export default function HeroSection() {
  const { getRootProps, getInputProps } = useDropzone({
    accept: {
      'video/mp4': ['.mp4'],
    },
    onDrop: (acceptedFiles) => {
      console.log(acceptedFiles);
    },
  });

  const [isScrolled, setIsScrolled] = useState(false);

  useEffect(() => {
    const handleScroll = () => {
      setIsScrolled(window.scrollY > 50);
    };

    window.addEventListener('scroll', handleScroll);
    return () => window.removeEventListener('scroll', handleScroll);
  }, []);

  return (
    <motion.section
      initial={{ opacity: 0, y: 40 }}
      animate={{ opacity: 1, y: 0 }}
      transition={{ duration: 0.8, ease: 'easeOut' }}
      className="min-h-screen px-4 py-16 mx-auto"
    >
      <div className="flex flex-col items-center justify-center space-y-6 text-center z-10">
        <motion.h1
          initial={{ opacity: 0, y: -20 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ delay: 0.2, duration: 0.8 }}
          className="text-4xl sm:text-5xl md:text-6xl font-extrabold text-gray-900 dark:text-white tracking-tight"
        >
          Convert MP4 to MP3 with Ease
        </motion.h1>

        <motion.p
          initial={{ opacity: 0, y: 20 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ delay: 0.4, duration: 0.8 }}
          className="text-lg sm:text-xl md:text-2xl text-gray-600 dark:text-gray-400 max-w-3xl"
        >
          Fast, secure, and high-quality MP4 to MP3 conversion without the need to sign up. Simple, seamless, and user-friendly.
        </motion.p>

        {/* DropZone with animation and hover */}
        <motion.div
          {...getRootProps()}
          whileHover={{ scale: 1.02 }}
          whileTap={{ scale: 0.98 }}
          initial={{ opacity: 0, scale: 0.95 }}
          animate={{ opacity: 1, scale: 1 }}
          transition={{ delay: 0.6, duration: 0.5 }}
          className="cursor-pointer bg-gray-50 dark:bg-gray-700 border-4 border-dashed border-gray-300 dark:border-gray-600 rounded-2xl p-12 text-gray-900 dark:text-white w-full sm:w-11/12 md:w-3/4 lg:w-2/3 mx-auto transition-all duration-300 shadow-lg hover:shadow-xl hover:border-blue-400"
        >
          <input {...getInputProps()} />
          <p className="text-lg md:text-xl font-medium">
            ðŸŽ¯ Drag and drop your MP4 file here or click to select
          </p>
        </motion.div>

        {/* Buttons */}
        <motion.div
          initial={{ opacity: 0, y: 30 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ delay: 0.8, duration: 0.6 }}
          className="space-x-4 sm:space-x-6 mt-12"
        >
          <motion.a
            whileHover={{ scale: 1.1 }}
            whileTap={{ scale: 0.95 }}
            href="#"
            className="bg-gradient-to-r from-indigo-500 to-blue-600 text-white py-3 px-8 rounded-full text-lg sm:text-xl shadow-md hover:shadow-xl transition-all duration-300"
          >
            ðŸš€ Convert Now
          </motion.a>

          <motion.a
            whileHover={{ scale: 1.1 }}
            whileTap={{ scale: 0.95 }}
            href="#pricing"
            className="py-3 px-8 rounded-full text-lg sm:text-xl border-2 border-gray-900 dark:border-white text-gray-900 dark:text-white hover:bg-gray-100 dark:hover:bg-white/10 transition-all duration-300 shadow-md hover:shadow-lg"
          >
            ðŸ’Ž Upgrade
          </motion.a>
        </motion.div>
      </div>
    </motion.section>
  );
}
